html {
  font-size: 62.5%;
  height: 100%;
  scroll-behavior: smooth;
}

body {
  color: #333;
  font-size: 1.4rem;
  font-family: "Helvetica Neue", Arial, "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
  overflow-x: hidden;
  line-height: 1.5;
}
@media (min-width: 768px) {
  body {
    font-size: 1.6rem;
  }
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

a {
  text-decoration: none;
  transition: opacity 0.4s;
  cursor: pointer;
  color: #333;
}
@media (hover: hover) and (pointer: fine) {
  a:hover {
    text-decoration: none;
    opacity: 0.7;
  }
}

.c-section {
  padding: 60px 0;
}
.c-section__title {
  font-size: 4rem;
  font-weight: 500;
  margin-bottom: 1.5rem;
}
@media (min-width: 768px) {
  .c-section__title {
    font-size: 6rem;
    margin-bottom: 2.5rem;
  }
}
.c-section__title + p {
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .c-section__title + p {
    margin-bottom: 4rem;
  }
}

.c-container {
  width: 90%;
  margin: 0 auto;
}

.p-experiments {
  column-count: 1;
}
@media (min-width: 768px) {
  .p-experiments {
    column-count: 2;
  }
}
.p-experiments__content a {
  background: #fff;
  padding: 1rem 0;
  display: block;
  position: relative;
  z-index: 1;
  border-top: 1px solid #000;
  border-bottom: 1px solid #000;
  margin-top: -1px;
  display: flex;
  align-items: center;
}
.p-experiments__content a img {
  width: auto;
  height: 60px;
  margin-right: 10px;
}
.p-experiments__content em {
  font-weight: 300;
  font-size: 1.2rem;
  font-style: normal;
  display: inline-block;
  width: 17px;
}

.js-lazyload {
  transition: opacity 2s;
  opacity: 0;
}
.js-lazyload.lazyloaded {
  opacity: 1;
  animation-name: kf-cover-slide;
  animation-duration: 1.6s;
  animation-timing-function: ease-in-out;
  animation-delay: 0s;
  animation-iteration-count: 1;
  animation-direction: normal;
  animation-fill-mode: forwards;
}

@keyframes kf-cover-slide {
  0% {
    transform-origin: left;
    transform: scaleX(0);
  }
  50% {
    transform-origin: left;
    transform: scaleX(1);
  }
  50.1% {
    transform-origin: right;
    transform: scaleX(1);
  }
  100% {
    transform-origin: right;
    transform: scaleX(0);
  }
}