@use "../mixins" as mixins;

.js-lazyload {
  transition: opacity 2s;
  opacity: 0;

  &.lazyloaded {
    opacity: 1;
    @include mixins.animation(
      $name: kf-cover-slide,
      $duration: 1.6s,
      $timing-function: ease-in-out
    );
  }
}

@keyframes kf-cover-slide {
  0% {
    transform-origin: left;
    transform: scaleX(0);
  }
  50% {
    transform-origin: left;
    transform: scaleX(1);
  }
  50.1% {
    transform-origin: right;
    transform: scaleX(1);
  }
  100% {
    transform-origin: right;
    transform: scaleX(0);
  }
}
